import React from 'react';

const ShiftPopup = ({ 
  isOpen,
  shiftTypes,
  selectedCells,
  bulkEditMode,
  onShiftChange,
  onClose
}) => {
  if (!isOpen) return null;

  return (
    <div className="popup-overlay" onClick={onClose}>
      <div className="popup" onClick={(e) => e.stopPropagation()}>
        <h3>
          {bulkEditMode && selectedCells.size > 0 
            ? `Выберите тип смены для ${selectedCells.size} ячеек` 
            : 'Выберите тип смены'
          }
        </h3>
        <div className="shift-options">
          {Object.entries(shiftTypes).map(([key, shiftType]) => (
            <div key={key} className={`shift-option shift-${key}`} onClick={() => onShiftChange(key)}>
              {shiftType.label}{shiftType.time && <br />}{shiftType.time}
            </div>
          ))}
          <div 
            className="shift-option" 
            style={{ background: '#fff', color: '#333', border: '2px solid #ddd' }}
            onClick={() => onShiftChange('clear')}
          >
            Очистить
          </div>
        </div>
        <div className="popup-buttons">
          <button className="btn btn-cancel" onClick={onClose}>Отмена</button>
        </div>
      </div>
    </div>
  );
};

export default ShiftPopup;