import React from 'react';
import { dayLabels } from '../constants/defaultData';

const Header = ({ 
  currentView, 
  selectedDay, 
  bulkEditMode,
  onViewSwitch,
  onBulkEditToggle,
  onSettingsOpen,
  onExportData,
  onImportData,
  onClearAllData,
  connectionState,
  onlineUsers,
  websocketEnabled
}) => {
  const getDayLabel = (dayIndex) => {
    return dayLabels[dayIndex] || '';
  };

  return (
    <div className="header">
      <div className="header-left">
        <h1>Расписание смен</h1>
        {websocketEnabled && (
          <div className={`connection-status ${connectionState}`}>
            {connectionState === 'connected' && '🟢 Подключено'}
            {connectionState === 'connecting' && '🟡 Подключение...'}
            {connectionState === 'disconnected' && '🔴 Отключено'}
            {connectionState === 'failed' && '❌ Ошибка'}
            {onlineUsers.size > 1 && ` (${onlineUsers.size})`}
          </div>
        )}
      </div>
      
      <div className="header-controls">
        <div className="view-toggle">
          <button 
            className={`view-btn ${currentView === 'grid' ? 'active' : ''}`}
            onClick={() => onViewSwitch('grid')}
          >
            Календарь
          </button>
          {selectedDay !== null && (
            <button 
              className={`view-btn ${currentView === 'gantt' ? 'active' : ''}`}
              onClick={() => onViewSwitch('gantt')}
            >
              Ганта ({getDayLabel(selectedDay)})
            </button>
          )}
        </div>
        
        <div className="data-controls">
          <button 
            className={`bulk-edit-btn ${bulkEditMode ? 'active' : ''}`}
            onClick={onBulkEditToggle}
          >
            {bulkEditMode ? 'Выйти из массового режима' : 'Массовое редактирование'}
          </button>
          
          <div className="dropdown">
            <button className="dropdown-btn">⚙️</button>
            <div className="dropdown-content">
              <button onClick={onSettingsOpen}>⚙️ Настройки</button>
              <button onClick={onExportData}>📥 Экспорт данных</button>
              <label className="import-btn">
                📤 Импорт данных
                <input 
                  type="file" 
                  accept=".json" 
                  onChange={onImportData}
                  style={{ display: 'none' }}
                />
              </label>
              <button onClick={onClearAllData} className="danger-btn">🗑️ Очистить все</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;